import React from 'react';
import classes from './Comment.module.css';
const Comment=(props)=>{

    return(
        <div className={classes.Comment} >
        <div>
        <div><img style={{width:'24px'}} src={props.img} /></div>
        <div>{props.name}</div>
        </div>
        <div>
        {props.children}
        </div>
        </div>
    )
}

export default Comment;